/* App.css */

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  text-align: center;
  font-family: sans-serif;
  background-color: #ffffff;
  padding: 20px;
}

.question-block,
.result-block {
  max-width: 500px;
  width: 100%;
  background-color: #f9f9f9;
  border-radius: 16px;
  padding: 30px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
}

.question-block h2,
.result-block h2 {
  margin-bottom: 20px;
  font-size: 24px;
  color: #333;
}

.question-block button {
  display: block;
  width: 100%;
  margin: 10px 0;
  padding: 12px 20px;
  font-size: 16px;
  border: none;
  border-radius: 8px;
  background-color: #4a76a8;
  color: white;
  cursor: pointer;
  transition: background 0.3s;
}

.question-block button:hover {
  background-color: #3b5f85;
}

.result-block img {
  max-width: 100%;
  height: auto;
  border-radius: 16px;
  margin: 20px 0;
}

.result-block p {
  font-size: 16px;
  color: #555;
  line-height: 1.5;
}
.fade-in {
  animation: fadeIn 0.7s ease-in;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

